html{height:100%}body{margin:0;padding:0;height:100%}#map{width:100%;height:100%;min-height:100%}.map-area{stroke:#555555;stroke-width:0.7;fill-opacity:0.5}.map-area:hover{fill-opacity:0.8}.map-area.pref{fill:#6ac6e3}.map-area.city{fill:#a9f17d}.map-area.town{fill:#f1f07d}.map-area.villege{fill:#fc8626}.map-area.ward{fill:#d41818}.legend{background-color:rgba(255,255,255,0.8);padding:10px;border-radius:5px}.legend p{margin-top:0;margin-bottom:2px;font-weight:bold}.legend div{padding:0 2px 0 2px;margin-top:3px;text-align:center;width:100%}.legend .pref{background-color:rgba(106,198,227,0.5)}.legend .city{background-color:rgba(169,241,125,0.5)}.legend .town{background-color:rgba(241,240,125,0.5)}.legend .villege{background-color:rgba(252,134,38,0.5)}.legend .ward{background-color:rgba(212,24,24,0.5)}
